<template>
  <div id="app">
    <img src="./assets/top.jpg" alt="商品主图">
    <Header></Header>
    <router-view name="slider"></router-view>    
    <transition mode="out-in" :name="names">
      <router-view class="mainBackground"/>
    </transition>
    <Footer></Footer>
  </div>
</template>

<script>
import Header from '@/components/common/Header'
import Footer from '@/components/common/Footer'
import slider from '@/components/common/slider'

export default {
  name: 'App',
  watch:{
    $route(to,from){
        if(to.meta.index < from.meta.index){
            this.names='left'
        }else{this.names='right'}
    }
  },
  data(){
      return{
          names:''//设置一个默认值
      } 
  },
  components:{
    Header,
    Footer,
    slider
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

body{
  margin:0px;
}

img{
    width:100%;
}

.left-enter,.right-leave-to{
    transform:translateX(-100%);
}
.left-leave-to,.right-enter{
    transform:translateX(100%);
}
.left-enter-active,
.left-leave-active,
.right-enter-active,
.right-leave-active{
    transition:.2s;
}
</style>



