<template>
  <div class="header">
      <ul><!-- 三种 router-link 绑定链接方式 -->
          <!-- tag 是改变标签默认元素 -->
          <!-- event 是事件监听，后面跟上事件，局部添加，哪里需要加在哪里 -->
          <!-- active-class 是局部设置这个标签的动态激活下的样式自定义名称，全局动态激活下样式在router/index.js中添加linkActiveClass:自定义名称 -->
          <!-- exact是精确激活，当激活其他组件的时候，默认组件的激活状态就会取消 -->
          <router-link to="/" tag="li" event="mouseover" active-class="activeClass1" exact >套餐</router-link>

          <router-link :to="{path:'/blog'}" tag="li" event="mouseover" active-class="activeClass2">博客</router-link>

          <router-link :to="{path:'/about'}" tag="li"  event="mouseover" active-class="activeClass">团队</router-link>

          <router-link :to="{path:'/product'}" tag="li"  event="mouseover" active-class="activeClass">商品</router-link>            
        </ul>
  </div>
</template>

<script>
export default {
    name: 'header'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul{   
    display:flex;
    width:100%;   
}

li{  
    flex:1;
}
</style>


