<template>
  <div class="about">
      <hr>
    <ul class="nav">
        <!-- 二级路径写法，其中a标签不用href属性，自动会添加 -->
        <!-- 第一个设置默认子路由，所以直接些about就可以了，在index.js文件中也要这样设置 -->
        <router-link to="/about" tag="li" event="mouseover"><a>文化</a></router-link>
        <router-link to="/work" tag="li" event="mouseover"><a>工作</a></router-link>
        <router-link to="/hobby" tag="li" event="mouseover"><a>兴趣</a></router-link>
    </ul>     
    <!-- 通过router-view把子路由渲染出来 -->
    <transition mode="out-in" :name="names">
        <router-view></router-view>  
    </transition>       
  </div>
</template>

<script>
export default {
    name: 'about',
    watch:{
        $route(to,from){
            if(to.meta.index < from.meta.index){
                this.names='left'
            }else{this.names='right'}
        }
    },
    data(){
        return{
            msg: '关于我们',
            names:''
        } 
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.nav{
    margin-top:-10px;
}

ul{   
    display:flex;
    width:100%;   
}

li{  
    flex:1; 
}

.left-enter,.right-leave-to{
    transform:translateX(-100%);
}
.left-leave-to,.right-enter{
    transform:translateX(100%);
}
.left-enter-active,
.left-leave-active,
.right-enter-active,
.right-leave-active{
    transition:.2s;
}
</style>


