import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Blog from '@/components/Blog'
import About from '@/components/About'
import noFound from '@/components/404'

import study from '@/views/study'
import work from '@/views/work'
import hobby from '@/views/hobby'

import slider from '@/components/common/slider'

Vue.use(Router)

let router = new Router({
  mode:'history',
  linkActiveClass: 'is-active',
  scrollBehavior(to,from,savePosition){
    //坐标定位,记录用户浏览到的位置
    if(savePosition){
      return savePosition
    }
    // else{
    //   return {x:0,y:0}
    // }
    // if(to.hash){
    //   return {
    //     selector:to.hash
    //   }
    // }else if(savePosition){
    //   return savePosition
    // }
    // //hash定位，给路径加上id，定位到id位置
  },
  routes: [
    {
      path: '/',
      name: 'Home',
      components: {
        default:Home,
        slider},
      meta:{
        index:0,
        title:'首页'
      },
      alias: '/index' //路由别名，可以在重定向时地址栏保留这个名字，但是该名字下没有原来的样式
    },
    {
      path: '/blog',
      name: 'Blog',
      component: Blog,
      meta:{
        index:1,
        title:'博客'
      },      
    }, 
    {
      path: '/product/:tip?/:productId?',
      meta:{
        index:5,
        title:'商品'
      },
      component: ()=>import ('@/components/Product')
      //这种引入组件方式不用取名
    },
    {
      path: '/about',
      component: About,
      children: [
        {
          path: '', //默认子路由
        component: study,
        meta:{
          index:2,
          title:'文化'
        }      
        },
        {
          path: '/work',
        component: work,
        meta:{
          index:3,
          title:'工作'
        } 
        },
        {
          path: '/hobby',
        component: hobby,
        meta:{
          index:4,
          title:'兴趣'
        } 
        },         
      ]
    }, 
    {
      path: '*',  
      // 404页面提示    
      // component: noFound
      // 重定向到指定页面的四个方法:

      // 方法4、动态设置重定向目标，to为目标路由对象，就是访问的路径的路由信息
      // redirect: to=>{
      //   return {path: '/blog'}
        // if(to.path === '/index'){
        //   return '/' // 方法1、直接指定路经
        // }
        // else if(to.path === '/news'){
        //   return {path: '/blog'} // 方法2、对象法
        // }
        // else{
        //   return {name: 'About'} // 方法3、对象属性名法
        // }
      // }
      
    }  
  ]
})

 router.afterEach((to,from)=>{
    if(to.meta.title){
      window.document.title=to.meta.title
    }else{window.document.title='FoodGood'}
  })
export default router;


